<template>
  <el-table id="tab" height="300" :data="tableData">
    <el-table-column prop="name" label="Live Node Name"/>
    <el-table-column prop="addr" label="Live Node Address"/>
    <el-table-column label="Operations">
      <template #default="scope">
        <el-button v-if="scope.$index === 0" size="small" @click="showUpload">Upload</el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-dialog class="custom-dialog" v-model="dialogVisible" title="Add Address To White List" width="35%" draggable>
    <el-input class="name" v-model="path" placeholder="Please input custom path" clearable size="large"/>

    <el-upload
        class="upload-demo"
        drag
        :action="fileServerAddress"
        multiple
        :data="{ 'path' : path }"
    >
      <el-icon class="el-icon--upload" :size="size" :color="color">  <UploadFilled /> </el-icon>
      <div class="el-upload__text">
        Drop file here or <em>click to upload</em>
      </div>
      <template #tip>
        <div class="el-upload__tip">
          jpg/png files with a size less than 500kb
        </div>
      </template>
    </el-upload>

  </el-dialog>
</template>

<script>
import {getCurrentInstance} from "vue";
import config_ from "@/Config";
import { ElMessage   } from "element-plus";

import { UploadFilled } from '@element-plus/icons-vue'


export default {
  name: "LiveNodesTable",
  data(){
    return{
      tableData : [
          {name:"file_server",addr:"127.0.0.1"},
      ],
      parsedData:null,
      UploadFilled:UploadFilled,
      dialogVisible:false,
      path:"",
      fileServerAddress: config_.fileServerAddress+"/upload"
    }
  },
  methods: {
    getLiveNodes(){
      const self = this;
      this.axios.get(`${config_.apiAddress}/nodes`)
          .then(function (data) {
            const liveNode = data.data.urls
            for (const key in liveNode) {
              let tmpDate = {
                name:key,
                addr:liveNode[key],
              }
              self.tableData.push(tmpDate)
            }
          })
          .catch(function (error) {
            console.error(error);
            ElMessage({
              message: error,
              type: 'error',
            });
          });
      // console.log(this.tableData)
    },
    showUpload(){
      this.dialogVisible = true;
    }

  },
  setup(){
    const getAxios = ()=>{
      return getCurrentInstance().appContext.config.globalProperties.$http
    }
    return {
      getAxios
    }
  },
  mounted() {
    this.axios = this.getAxios();
    this.getLiveNodes()
  },
}
</script>

<style>
#tab {
  width: 60%;
  margin: 0 auto;
  /*background-color: #047e7e;*/
  border-radius: 8px;
}
.el-table .el-table__header-wrapper {
  background-color: #047e7e;
}

.custom-dialog{
  border-radius: 8px;
}
</style>
