<template>
  <el-button type="primary" :icon="AddLocation" @click="dialogVisible = true">Add</el-button>

  <el-dialog class="custom-dialog" v-model="dialogVisible" title="Add Address To White List" width="30%" draggable>
    <el-input class="name" v-model="Name" placeholder="Please input name" clearable size="large"/>
    <el-input class="addr" v-model="Address" placeholder="Please input address" clearable size="large"/>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="addWhiteAddress">
          Send
        </el-button>
      </span>
    </template>
  </el-dialog>
<el-button type="primary" :icon="Monitor" @click="dialogVisible1 = true">Check</el-button>

  <el-dialog class="custom-dialog" v-model="dialogVisible1" title="Check Node Live Status" width="30%" draggable>
    <el-input class="name" v-model="Hostname" placeholder="Please input Hostname" clearable size="large"/>
    <el-input class="addr" v-model="Port" placeholder="Please input Port" clearable size="large"/>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible1 = false">Cancel</el-button>
        <el-button type="primary" @click="checkLiveNode">
          Check
        </el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { AddLocation ,Monitor} from '@element-plus/icons-vue'
import { getCurrentInstance } from "vue";
import config_ from "@/Config";
import { ElMessage,ElMessageBox  } from 'element-plus'

export default {
  name: "AddValidAddressButton",
  data() {
    return {
      AddLocation: AddLocation,
      Monitor: Monitor,
      dialogVisible: false,
      dialogVisible1: false,
      Name:"",
      Address:"",
      Hostname:"",
      Port:"",
    };
  },
  setup(){
    const getAxios = ()=>{
      return getCurrentInstance().appContext.config.globalProperties.$http
    }
    return {
      getAxios
    }
  },
  mounted() {
    this.axios = this.getAxios();
  },
  methods: {
    addWhiteAddress (){
      this.axios.get(`${config_.apiAddress}/import?urls.${this.Name}=${this.Address}`)
          .then(function (data) {
            ElMessage({
              message: data.data,
              type: 'success',
            })
          })
          .catch(function (error) {
            // 请求失败的处理
            console.error(error);
            ElMessage({
              message: error,
              type: 'error',
            });
            // 可以执行其他适当的操作来处理错误
          });
      this.dialogVisible = false;
    },
    checkLiveNode(){
      this.axios.get(`${config_.apiAddress}/check?hostname=${this.Hostname}&port=${this.Port}`)
          .then(function (data) {
            if (data.data.success) {
              ElMessageBox.alert(
                  data.data.message,
                  'Node survival',
                  {
                    dangerouslyUseHTMLString: true,
                  }
              )
            }else {
              new Error(data.data.message)
            }

          })
          .catch(function (error) {
            // 请求失败的处理
            ElMessage({
              message: error,
              type: 'error',
            });
            // 可以执行其他适当的操作来处理错误
          });
      this.dialogVisible1 = false;
    }
  },
}
</script>

<style>
.custom-dialog{
  border-radius: 8px;
}
</style>
